@import url('https://fonts.googleapis.com/css2?family=Cabin:wght@400;600&family=Roboto+Condensed:wght@400;700&display=swap');

* {
  margin: 0 auto;
  box-sizing: border-box;
  font-family: 'Roboto Condensed', sans-serif;
}

/* Fonts */

.font-light {
  font-weight: 400;
}

.font-bold {
  font-weight: 700;
}

.main-screen-wrapper {
  width: 1200px;
  height: 600px;
  margin: 80px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.side-panel {
  width: 300px;
  height: 600px;
  background: lightgray;
  padding: 10px;
  border-radius: 10px;
  color: white;
}

.left-panel {
  background: #3E5C76;
}

.right-panel {
  background: #3E5C76;
}

.main-panel-wrapper {
  width: 100%;
  height: 100%;
}

.main-panel {
  width: 600px;
  height: 600px;
  background: #1D2D44;
  padding: 10px;
  color: white;
  border-radius: 10px;
  margin: 2px;
}

/* Left pannel - ship*/

.cargo-bay {
  width: 235px;
  height: 200px;
  background: #748CAB;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  border-radius: 10px;
  padding: 5px;
  margin: 10px auto;
}

.cargo-crate-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.cargo-crate {
  width: 20px;
  height: 20px;
  background: blanchedalmond;
  margin: 4px;
  text-align: center;
  border-radius: 2px;
  box-shadow: 2px 2px 0px 0px black;
}

.hold-capacity {
  position: absolute;
  top: 350px;
}

.titanium {
  background: #118AB2;
}

.uranium {
  background: #06D6A0;
}

.ship-stats {
  margin: 10px;
}



/* Right panel - planet */

.market-place {
  margin: 20px 0 0 0;
}

/* Main panel */

.planet-position {
  position: relative;
  display: inline;
}

.dplanet-circle {
  content: ".";
  position: relative;
  top: -20px;
  left: 25px;
}
